.header-container {
    background-color: var(--secondary);
    height: 3rem;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}

.title-container {
    margin-left: 3em;
    white-space: nowrap;
    overflow: scroll;
    width: 30%;
}
.nav-container {
    margin-right: 0.5rem;
    display: flex;
    flex-direction: row;
    width: 50%;

}

.title-labels {
    margin: 0.5em;
}
